import { render } from '@testing-library/react'
import React from 'react'
import { findRenderedDOMComponentWithClass, scryRenderedComponentsWithType } from 'react-dom/test-utils'
import ButtomMove from './ButtomMove'
import ImgLbox from './ImgLbox'
import GaleriaLightBox from '../../components/layout/GaleriaLightBox'
import './Galeria.scss'

var Galeria = props => {

    let imgs = []
    let imgsLbox = []

    for (let i = 1; i <= props.fotos; i++) {
        imgs.push(<ImgLbox style={"bg-" + i + "-G" + props.galeria + " galeria" + props.galeria} onClick={"#"+props.nomenc + i}></ImgLbox>)
        imgsLbox.push(<GaleriaLightBox foto={i} qtd_fotos={props.fotos} nomenc={props.nomenc}></GaleriaLightBox>)
    }

    return (
        <div>
            <div className="gallery">
                <ButtomMove nonce={"galeria" + props.galeria} style='prev' onClick={props.handlePrev} />
                <ButtomMove nonce={"galeria" + props.galeria} style='next' onClick={props.handleNext} />
                <div className={"gallery__stream galeria__stream__galeria" + props.galeria}>
                    {imgs}
                </div>
            </div>
            {imgsLbox}
        </div>
    )
}

export default Galeria