import React, { Component } from 'react'
import './Projeto.scss'
import '../../components/layout/gallery.min.css'
import '../../components/layout/gallery.theme.css'
import '../../components/layout/gallery.prefixed.min.css'
import Galeria from '../../components/layout/Galeria'

export default class Projeto extends Component {
    
    handlePrev(obj){
        var nomeGaleria = obj.target.nonce;
        var stream = document.querySelector('.galeria__stream__'+nomeGaleria);
        var items = document.querySelectorAll('.'+nomeGaleria);
        stream.insertBefore(items[items.length - 1], items[0]);
    }

    handleNext(obj){
        var nomeGaleria = obj.target.nonce;
        var stream = document.querySelector('.galeria__stream__'+nomeGaleria);
        var items = document.querySelectorAll('.'+nomeGaleria);
        stream.appendChild(items[0]);
    }

    render() {
        return (
            <div className="Projeto">
                <div className="container">
                    <h1>Projetos</h1>

                    {/* Projeto 1 */}
                    <div className="box-projeto">
                        <div className="box-left">
                            <div className="box-galeria w-sm">
                                <Galeria galeria="1" fotos="4" nomenc="jobs" handlePrev={this.handlePrev} handleNext={this.handleNext}></Galeria>
                            </div>
                        </div>
                        <div className="box-right">
                            <div className="box-descricao">
                                <div className="title">Projeto 1 | Melhorias de Rede</div>
                                <div className="descricao">
                                    <p>Este projeto foi elaborado com o objetivo de melhorar a estrutura de rede da empresa.</p>
                                    <ul>
                                        <li>Passagem e organização de cabos CAT5e, CAT6 e Fibra</li>
                                        <li>Certificação dos cabos e conectores de rede</li>
                                        <li>Remanejo e instalação de novos ativos de rede</li>
                                        <li>Organização e identificação dos racks</li>
                                        <li>Identificação dos pontos de rede</li>
                                        <li>Configuração dos switches gerenciáveis</li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>

                    {/* Projeto 2 */}
                    <div className="box-projeto">
                        <div className="box-right">
                            <div className="box-descricao">
                                <div className="title">Projeto 2 | Monitoramento</div>
                                <div className="descricao">
                                    <p>Devido ao grande número de servidores e computadores foi necessário implementar um sistema de monitoramento para proporcionar um controle de tráfego na rede de computadores da empresa</p>
                                    <ul>
                                        <li>Instalação e configuração do Zabbix Server para monitoramento de rede e desempenho de servidores</li>
                                        <li>Implementação de sistema de gráficos em conjunto com Zabbix para melhor visualizar o monitoramento</li>
                                        <li>Configuração de envio de alertas para eventual problemas ou parada de equipamentos ou links de internet</li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                        <div className="box-left">
                            <div className="box-galeria w-sm">
                                <Galeria galeria="2" fotos="4" nomenc="redes" handlePrev={this.handlePrev} handleNext={this.handleNext}></Galeria>
                            </div>
                        </div>
                    </div>

                    {/* Projeto 3 */}
                    <div className="box-projeto">
                        <div className="box-left">
                            <div className="box-galeria w-sm">
                                <Galeria galeria="3" fotos="2" nomenc="telefonia" handlePrev={this.handlePrev} handleNext={this.handleNext}></Galeria>
                            </div>
                        </div>
                        <div className="box-right">
                            <div className="box-descricao">
                                <div className="title">Projeto 3 | Telefonia IP</div>
                                <div className="descricao">
                                    <p>Mudança de telefonia de Analógica para Digital</p>
                                    <ul>
                                        <li>Elaboração do novo projeto de telefonia</li>
                                        <li>Negociação sobre plataforma e serviços de telefonia com parceiros e empresas fornecedoras do produto</li>
                                        <li>Preparação da infra estrutura de rede para adaptação da telefonia IP</li>
                                        <li>Administrador dos sistemas de telefonia</li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>

                    {/* Projeto 4 */}
                    <div className="box-projeto">
                        <div className="box-right">
                            <div className="box-descricao">
                                <div className="title">Projeto 4 | Active Directory</div>
                                <div className="descricao">
                                    <p>Implementação do sistema de acesso Active Directory na empresa</p>
                                    <ul>
                                        <li>Elaboração do projeto de Active directory</li>
                                        <li>Instalação e configuração do servidor AD</li>
                                        <li>Administrador das Políticas de GPOs do AD</li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                        <div className="box-left">
                            <div className="box-galeria w-sm">
                                <Galeria galeria="4" fotos="2" nomenc="ad" handlePrev={this.handlePrev} handleNext={this.handleNext}></Galeria>
                            </div>
                        </div>
                    </div>

                    {/* Projeto 5 */}
                    <div className="box-projeto">
                        <div className="box-left">
                            <div className="box-galeria w-sm">
                                <Galeria galeria="5" fotos="3" nomenc="intra" handlePrev={this.handlePrev} handleNext={this.handleNext}></Galeria>
                            </div>
                        </div>
                        <div className="box-right">
                            <div className="box-descricao">
                                <div className="title">Projeto 5 | Intranet</div>
                                <div className="descricao">
                                    <p>Desenvolvimento da Intranet e sistema de chamados para suporte técnico</p>
                                    <ul>
                                        <li>Elaboração da Intranet</li>
                                        <li>Desenvolvimento e integração digital com fluxo de trabalho</li>
                                        <li>Criação e integração do sistema de chamados ao suporte técnicos com a intranet</li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="top-site">
                    <a href="#topo" title="ir para o topo">
                        <i className="fa fa-angle-double-up fa-3x"></i>
                    </a>
                </div>
            </div>
        )
    }
}