import React from "react"
import "./Home.scss"
import fotoWeslley from "../../components/images/foto1.jpg"
import { Link } from 'react-router-dom'

const Home = props => (
    <div className="Home">
        <div className="container box-home">
            <div className="box-img">
                <div className="img-foto">
                    <img src={fotoWeslley} alt="" className="img-fluid"></img>
                </div>
            </div>
            <div className="box-right">
                <div>
                    <h1>Olá,</h1>
                    <p>um pouco sobre mim:</p>
                </div>
                <div className="box-circle">
                    <Link to="/curriculo" className="circle-item red">Meu currículo</Link>
                    <Link to="/projetos" className="circle-item blue">Meus trabalhos</Link>
                    <Link to="/curriculo" className="circle-item yellow">Meus conhecimentos</Link>
                </div>
                <div className="box-texto">
                    <p><i className="fa fa-quote-left"></i> Sou um profissional que atua a mais de 20 anos na área de Tecnologia. Sou formado em Análise e Desenvolvimento de Sistemas pela Unopar. Ao longo de todos esses anos tive um aprendizado muito grande, sempre procurando novos desafios onde eu possa aprender e contribuir junto a empresa e as pessoas que fazem parte do meu dia a dia. <i className="fa fa-quote-right"></i></p>
                </div>
            </div>
        </div>
    </div>
)

export default Home