import React from 'react'

const GaleriaLightBox = props => {

    let passo_prev = props.foto-1;
    let passo_next = props.foto+1;
    
    if(props.foto == 1){
        passo_prev = props.qtd_fotos
    }
    if(props.foto == props.qtd_fotos){
        passo_next = 1
    }

    return (
        <div className="lbox" id={props.nomenc + props.foto}>
            <div className="box_img">
                <img src={'/assets/'+props.nomenc+props.foto+'.jpg'}></img>
                <a href="#" id="close"><i className="fa fa-times-circle fa-2x"></i></a>
                <a href={"#"+props.nomenc+(passo_prev)} id="prev"><i className="fa fa-angle-double-left fa-3x"></i></a>
                <a href={"#"+props.nomenc+(passo_next)} id="next"><i className="fa fa-angle-double-right fa-3x"></i></a>
            </div>
        </div>
    )
}
export default GaleriaLightBox