import React from "react"
import "./Menu.scss"
import { Link } from 'react-router-dom'

const Menu = props => (
    <div className="Menu">
        <div className="container box-nome">
            <div className="menu-hambu">
                <nav className="navbar navbar-light light-blue lighten-4 d-block d-md-none">
                    <button className="navbar-toggler" type="button" 
                            data-bs-toggle="collapse" data-bs-target="#navbarNavAltMarkup" aria-controls="navbarNavAltMarkup" 
                            aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
                        <div className="navbar-nav">
                            <Link to="/">Início</Link>
                            <Link to="/curriculo">Currículo</Link>
                            <Link to="/projetos">Projetos</Link>
                        </div>
                    </div>
                </nav>
            </div>
            <div className="dv-nome" id="topo">
                <div className="nome">Weslley O. Ribeiro</div>
                <div className="cargo">Tecnólogo em Análise e Desenv. de Sistemas</div>
            </div>
            <div className="dv-menu d-none d-lg-block ">    
                <nav>
                    <ul>
                        <li>
                            <Link to="/">Início</Link>
                        </li>
                        <li>
                            <Link to="/curriculo">Currículo</Link>
                        </li>
                        <li>
                            <Link to="/projetos">Projetos</Link>
                        </li>
                    </ul>
                </nav>
            </div>
            
        </div>
    </div>
)

export default Menu