import React from 'react'
import './Content.scss'
import { Switch, Route} from "react-router-dom"

import Home from '../../views/home/Home'
import Curriculo from '../../views/curriculo/Curriculo'
import Projeto from '../../views/projeto/Projeto'

const Content = props => (
    <div className="Content">
        <Switch>
            <Route exact path="/">
                <Home></Home>
            </Route>    
            <Route path="/curriculo">
                <Curriculo></Curriculo>
            </Route>    
            <Route path="/projetos">
                <Projeto></Projeto>
            </Route>    
        </Switch>
        {/* <div className="top-site">
            <a href="#topo" title="ir para o topo">
                <i className="fa fa-angle-double-up fa-3x"></i>
            </a>
        </div> */}
    </div>
)

export default Content